/**
 * Registers a new block provided a unique name and an object defining its behavior.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/#registering-a-block
 */
import { registerBlockType } from "@wordpress/blocks";

/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * All files containing `style` keyword are bundled together. The code used
 * gets applied both to the front of your site and to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import "./style.scss";

/**
 * Internal dependencies
 */
import Edit from "./edit";
import save from "./save";

/**
 * Every block starts by registering a new block type definition.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/#registering-a-block
 */
registerBlockType("inmo/inicio", {
	/**
	 * @see https://make.wordpress.org/core/2020/11/18/block-api-version-2/
	 */
	apiVersion: 2,

	/**
	 * This is the display title for your block, which can be translated with `i18n` functions.
	 * The block inserter will show this name.
	 */
	title: __("Inmo - Header", "inmo"),

	/**
	 * This is a short description for your block, can be translated with `i18n` functions.
	 * It will be shown in the Block Tab in the Settings Sidebar.
	 */
	description: __("Bloco Header INMO", "inmo"),

	/**
	 * Blocks are grouped into categories to help users browse and discover them.
	 * The categories provided by core are `text`, `media`, `design`, `widgets`, and `embed`.
	 */
	category: "common",

	/**
	 * An icon property should be specified to make it easier to identify a block.
	 * These can be any of WordPressâ€™ Dashicons, or a custom svg element.
	 */
	icon: "smiley",

	/**
	 * Optional block extended support features.
	 */
	supports: {
		// Removes support for an HTML mode.
		html: false,
	},
	attributes: {
		title: {
			type: "string",
			source: "text",
			selector: "h2.inicio-title",
			// default: "OlÃ¡, Ã© bom receber vocÃª aqui",
		},
		paragraph1: {
			type: "string",
			source: "html",
			selector: "p.inicio-line-1",
			// default:				"NÃ³s somos a Inmo, estamos aqui para ajudar vocÃª na construÃ§Ã£o do seu chatbot.",
		},
		paragraph2: {
			type: "string",
			source: "html",
			selector: "p.inicio-line-2",
			// default:"Converse com o <i>MÃ´</i>, o robÃ´ que criamos para entender a sua necessidade ;)",
		},
		buttonPrimaryText: {
			type: "string",
			source: "text",
			selector: "button.inicio-btn-primary",
			// default: "Falar com MÃ´",
		},
		buttonSecondaryText: {
			type: "string",
			attribute: "text",
			// source: "html",
			selector: "a.inicio-btn-secondary",
			// default: "Falar no WhatsApp",
		},
		buttonSecondaryUrl: {
			type: "string",
			source: "attribute", // binds an attribute of the tag
			attribute: "href", // binds href of a: the link url
			selector: "a.inicio-btn-secondary",
		},
	},

	/**
	 * @see ./edit.js
	 */
	edit: Edit,

	/**
	 * @see ./save.js
	 */
	save,
});
